@charset "UTF-8";

// @use './modules/index';

:root {
  --gradinet: linear-gradient(
    45deg,
    #845ec2,
    #d65db1,
    #ff6f91,
    #ff9671,
    #ffc75f,
    #f9f871
  );
}

* {
  outline: 0;
  margin: 0;
  padding: 0;
}

body {
  background-color: #222222;
  display: grid;
  place-items: center;
  height: 100vh;
}

.loading {
  &_text {
    font-family: Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    color: antiquewhite;
    text-align: center;
    margin-top: 20px;
  }
}

.spinner {
  position: relative;
  background-color: pink;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background: conic-gradient(pink, transparent);
  animation:  rotate 1.5s linear infinite;
  &::before,&::after {
    position: absolute;
    content: "";
    border-radius: 50%;
  }
  &::before {
    width: 80px;
    height: 80px;
    top: 10px;
    left: 10px;
    background-color: #222;
  }
  &::after {
    height: 10px;
    width: 10px;
    background-color: pink;
    top: 0;
    left: 45px;
  }
}

@keyframes rotate {
  from{ transform: rotateZ(0);}
  to{ transform: rotate(360deg);}
}


// 前回の
.container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: var(--gradinet);
  animation: bg_gradient 10s infinite alternate;
  background-size: 400%;
  .ball {
    position: absolute;
    width: 200px;
    height: 200px;
    background: var(--gradinet);
    transition: 5s;
    border-radius: 30% 70% 54% 46% / 30% 48% 52% 70%;
    filter: blur(20px);
    // filter: blur(20px) contrast(50) drop-shadow(16px 16px 20px gray);
  }
}

@keyframes bg_gradient{
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

.box {
  background-color: pink;
  width: 30px;
  height: 30px;
}